* {
  box-sizing: border-box;
}

body {
  background-color: #fff;
  justify-content: center;
  align-items: center;
  width: 100vw;
  height: 100vh;
  margin: 0;
  padding: 0;
  display: flex;
}

@keyframes move {
  from {
    transform: rotate(0);
  }

  to {
    transform: rotate(360deg);
  }
}

.watch {
  background-color: #c2d5d6;
  border: 1.28vmin double #000;
  border-radius: 50%;
  width: 80vmin;
  height: 80vmin;
  position: relative;
}

.watch__dot {
  z-index: 1;
  background-color: #fff;
  border-radius: 50%;
  width: 3.2vmin;
  height: 3.2vmin;
  position: absolute;
  top: 38.4vmin;
  left: 38.4vmin;
}

.watch__hour-arrow {
  transform-origin: bottom;
  background-color: #969595;
  border-radius: 1.6vmin;
  width: 1.6vmin;
  height: 24vmin;
  animation: 60s linear 2s infinite move;
  position: absolute;
  top: 16vmin;
  left: 39.2vmin;
}

.watch__min-arrow {
  transform-origin: bottom;
  background-color: #000;
  border-radius: .8vmin;
  width: .8vmin;
  height: 36vmin;
  animation: 5s linear 2s infinite move;
  position: absolute;
  top: 4vmin;
  left: 39.584vmin;
}

